<template>
  <div>
    <div class="tableHead">
      <div class="tableHead-left">
        <i class="el-icon-refresh"></i>
        <el-button type="text" style="margin-left:20px" @click="batchOperat"
          >
        </el-button>
      </div>
      <div class="tableHead-right">
        <el-button
          size="mini"
          style="height: 30px;margin:auto 50px "
          @click="searchClick"
          >
        </el-button>
        <template>
          <el-select
            v-model="selectBuild"
            placeholder=""
            size="mini"
            style="width:120px;"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </template>
     
      </div>
    </div>

    <template>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column
          type="expand"
          label=""
          width="78px"
          align="center"
          style="padding:0;height: 50px;"
        >
          <template slot-scope="props" style="padding:0">
            <el-form
              label-position="left"
              inline
              class="demo-table-expand"
              style="padding:0;height: 40px;"
            >
              <el-form-item label="IP:">
                <span>{{ props.row.ip }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.location }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.name }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="type" align="center">
        </el-table-column>
        <el-table-column label="" prop="brandge" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope"> {{ scope.row.ter }} </template>
        </el-table-column>
        <el-table-column label="" prop="status" align="center">
        </el-table-column>
        <el-table-column label="" prop="nums" align="center">
        </el-table-column>
        <el-table-column label="" prop="control" width="200" align="center">
          <template slot-scope="props">
            <el-select
              v-model="controlItem[props.$index]['control' + props.$index]"
              placeholder=""
              size="small"
              style="width:150px;"
            >
              <el-option
                v-for="item in props.row.control"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="" prop="selfControl" align="center">
          <template slot-scope="props">
            <el-switch
              v-model="props.row.selfControl"
              active-color="#13ce66"
              inactive-color="#ff3342"
              @change="switchChange($event, props.$index)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="selfControl"
          align="center"
          width="200"
        >
          <template slot-scope="props">
            <el-popover
              v-if="props.row.selfControl"
              placement="top-start"
              title=""
              width="100"
              trigger="hover"
              content=""
            >
              <el-switch
                slot="reference"
                disabled="!props.row.selfControl"
                v-model="props.row.jdControl"
                active-color="#13ce66"
                inactive-color="#ff3342"
                @change="
                  switchChange2($event, props.$index, props.row.selfControl)
                "
              >
              </el-switch>
            </el-popover>
            <el-switch
              v-if="!props.row.selfControl"
              slot="reference"
              v-model="props.row.jdControl"
              active-color="#13ce66"
              inactive-color="#ff3342"
              @change="
                switchChange2($event, props.$index, props.row.selfControl)
              "
            >
            </el-switch>
          </template>
        </el-table-column>
      </el-table>
    </template>
    <!--  -->

    <batchDialog
      :showBatch="showBatch"
      @batchOperatClose="batchOperatClose"
    ></batchDialog
    ><!--  -->
  </div>
</template>

<script>
import batchDialog from "./batchOperat";

export default {
  data() {
    return {
      /* */

      addName: "", //
      addIp: "", //ip
      addId: "", //
      addType: "", //
      addBrand: "", //
      addRoom: "", //
      addFloor: "", //
      addBuilding: "", //
      addSchoolAera: "", //

      addNameOptions: "", //
      addIpOptions: "", //ip
      addIdOptions: "", //
      addTypeOptions: "", //
      addBrandOptions: "", //
      addRoomOptions: "", //
      addFloorOptions: "", //
      addBuildingOptions: "", //
      addSchoolAeraOptions: "", //

      /*  */
      showDialog: false,
      type: "",
      room: "",
      floor: "",
      build: "",
      school: "",
      brand: "",
      id: "",
      ip: "",
      name: "",

      /*  */
      showUpLoad: false,

      /*  */
      showBatch: false, //
      centerDialogVisible: false, //
      selectBuild: "", //
      searchInput: "", //
      code: this.$route.query.code, //
      controlItem: "", //
      selfBtnValue: "",
      options: [
        //
        {
          value: "1F",
          label: ""
        },
        {
          value: "2F",
          label: ""
        },
        {
          value: "3F",
          label: ""
        },
        {
          value: "4F",
          label: ""
        },
        {
          value: "5F",
          label: ""
        }
      ],
      tableData: [
        //
        {
          type: "1P",
          brandge: "",
          ter: "26",
          status: "",
          nums: "200",
          control: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "20",
              label: "20"
            },
            {
              value: "18",
              label: "18"
            },
            {
              value: "18",
              label: "18"
            }
          ],
          selfControl: true,
          location: "209",
          name: "",
          id: "1123",
          ip: "172.16.22.18"
        },
        {
          type: "1P",
          brandge: "",
          ter: "26",
          status: "",
          nums: "200",
          control: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "20",
              label: "20"
            },
            {
              value: "18",
              label: "18"
            },
            {
              value: "18",
              label: "18"
            }
          ],
          selfControl: true,
          location: "209",
          name: "",
          id: "1123",
          ip: "172.16.22.18"
        },
        {
          type: "1P",
          brandge: "",
          ter: "26",
          status: "",
          nums: "200",
          control: [
            {
              value: "",
              label: ""
            },
            {
              value: "",
              label: ""
            },
            {
              value: "20",
              label: "20"
            },
            {
              value: "18",
              label: "18"
            },
            {
              value: "18",
              label: "18"
            }
          ],
          selfControl: "",
          location: "209",
          name: "",
          id: "1123",
          ip: "172.16.22.18"
        }
      ]
    };
  },
  components: {
    batchDialog
  },
  methods: {
    test() {
      console.log("");
    },
    buildControlModel() {
      //
      let arr = [];
      for (let i = 0; i < 10; i++) {
        arr.push({ ["control" + i]: "" });
      }
      return arr;
    },
    switchChange(el, id) {
      //
      console.log(el, id);
    },
    searchClick() {
      //
    },
    editClose() {
      //
      console.log("");
      this.showDialog = false;
    },
    batchOperatClose() {
      //
      console.log("");
      this.showBatch = false;
    },
    handleEdit(id, name) {
      //
      this.showDialog = true;
      console.log(id, name);
    },
    open(name) {
      //
      this.$confirm(`${name}, ?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleDelete(name, id) {
      //
      this.open(name);
    },
    batchOperat() {
      //
      this.showBatch = true;
    },
    batchExport() {
      //
      this.showUpLoad = true;
    },
    exportClose() {
      //
      this.showUpLoad = false;
    },
    openMessage() {
      this.$notify({
        title: "",
        message: "",
        type: "warning",
        duration: 0,
        onClick: this.checkedInfo
      });
    },
    checkedInfo() {
      this.$router.push("/waringInfo");
      console.log("");
    }
  },
  watch: {
    $route() {
      //
      this.code = this.$route.query.code;
      this.test();
    }
  },
  mounted() {
    this.controlItem = this.buildControlModel(); //
    this.openMessage();
  }
};
</script>

<style scoped>
.tableHead {
  height: 50px;
  border-bottom: 1px solid #000;
  line-height: 50px;
  /* padding: 0 0 0 5px; */
}

.tableHead .tableHead-left {
  display: inline-block;
  width: 49%;
}

.el-icon-refresh {
  float: left;
  margin-left: 20px;
  font-size: 34px;
  font-weight: 900;
  color: #5daf34;
  padding-top: 7px;
}

.tableHead-left {
  float: left;
}

.tableHead .tableHead-right {
  display: inline-flex;
  flex-direction: row-reverse;
  width: 50%;
}

.tableHead .tableHead-right .el-input-sellf {
  position: relative;
  font-size: 14px;
  display: inline-block;
  /* width: 100%; */
}

.tableHead .tableHead-right .tableSearch {
  border-radius: 5px;
  height: 40px;
  width: 50px;
}

.dialogContent {
  /* background: red; */
  height: 100%;
  width: 100%;
  text-align: center;
}

.dialogContent .dialogContentItem {
  height: 30px;
  margin: 0 0 20px 0;
}

.dialogContent .dialogContentItem .dItemText {
  margin-right: 15px;
}

.numStyle {
  margin: 0 0 20px -27px;
}

.ipStyle {
  margin: 0 0 20px -15px;
}

.nameStyle {
  margin: 0 0 20px -29px;
}
</style>



