<template>
  <el-dialog title=""
             top="110px"
             :visible.sync="manageGroups"
             :close-on-press-escape="false"
             :close-on-click-modal="false"
             :before-close="closeAllModel">
    <hr class="boundary">
    <div class="body">
      <el-card class="cardGroups">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <div v-for="(item,index) in allCampuses" class="campusesList"
             @click="selectCampus(campusType,item.id,item.name,index)">
          {{item.name}}
          <div v-show="showCampusOperate===index" class="operation">
            <i class="el-icon-edit" @click="modifyNewDiv(campusType,index,item.name)"></i>
            <i class="el-icon-delete" @click="deleteGroup(campusType,item.id)"></i>
          </div>
        </div>
        <div class="cardFooter"><i class="el-icon-circle-plus" @click="addNewDiv(campusType)"></i></div>
        <div v-show="haveCampuses" class="noData"></div>
      </el-card>
      <el-card class="cardGroups">
        <div slot="header" class="clearfix">
          <span>()</span>
        </div>
        <div v-for="(item,index) in buildingsData" class="buildingsList"
             @click="selectBuilding(buildingType,item.id,item.name,index)">
          {{item.name}} ({{item.floors}})
          <div v-show="showBuildingOperate===index" class="operation">
            <i class="el-icon-edit" @click="modifyNewDiv(buildingType,index,item.name,item.floors)"></i>
            <i class="el-icon-delete" @click="deleteGroup(buildingType,item.id)"></i>
          </div>
        </div>
        <div class="cardFooter"><i class="el-icon-circle-plus" @click="addNewDiv(buildingType)"></i></div>
        <div v-show="haveBuildings" class="noData"></div>
      </el-card>
      <el-card class="cardGroups">
        <div slot="header" class="clearfix">
          <span id="className"></span>
        </div>
        <div v-for="(item,index) in classroomsData" class="classroomsList"
             @click="selectClassroom(classroomType,item.id,item.name,index)">
          {{item.name}}
          <div v-show="showClassroomOperate===index" class="operation">
            <i class="el-icon-edit" @click="modifyNewDiv(classroomType,index,item.name)"></i>
            <i class="el-icon-delete" @click="deleteGroup(classroomType,item.id)"></i>
          </div>
        </div>
        <div class="cardFooter"><i class="el-icon-circle-plus" @click="addNewDiv(classroomType)"></i></div>
        <div v-show="haveClassrooms" class="noData"></div>
      </el-card>
    </div>
    <hr class="boundary">
    <del-group :delGroupType="delGroupType" :delGroup="delGroup" :delId="delId"
               :campusId="campusId" :buildingId="buildingId" @closeModel="closeModel"
               @getCampusesList="getCampusesList" @getBuildingsList="getBuildingsList"
               @getClassroomsList="getClassroomsList" @clearCSS="clearCSS"></del-group>
    <batch-addition :batchAddition="batchAddition" @closeModel="closeModel"></batch-addition>
    <div slot="footer" class="dialog-footer">
      <el-button type="success" @click="showBatchAddition"></el-button>
      <el-button @click="closeAllModel"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import delGroup from './groups/delGroup'
  import batchAddition from './groups/batchAddition'

  export default {
    components: {
      delGroup,
      batchAddition
    },
    data () {
      return {
        manageGroups: true,
        clickList: true,//
        haveCampuses: true,
        campusId: -1,
        campusType: 0,
        allCampuses: [],
        showCampusOperate: -1,
        haveBuildings: true,
        buildingId: -1,
        buildingType: 1,
        modifyBtn: true,
        buildingsData: [],
        showBuildingOperate: -1,
        haveClassrooms: true,
        classroomId: -1,
        classroomType: 2,
        classroomsData: [],
        showClassroomOperate: -1,
        delGroup: false,
        delId: 0,
        delGroupType: '',
        batchAddition: false,
      }
    },
    methods: {
      // 
      selectCampus (type, id, name, index) {
        // 
        if (this.clickList === true) {
          this.campusId = id// id
          // 
          this.buildingId = -1// id
          this.buildingsData = []
          this.classroomsData = []
          this.showBuildingOperate = -1// 
          this.haveClassrooms = true// 
          // 
          this.selectList(type, name, index)
          // 
          this.getBuildingsList(id)
        } else {
          return false
        }
      },

      // 
      selectBuilding (type, id, name, index) {
        if (this.clickList === true) {
          this.buildingId = id// id
          this.classroomsData = []
          this.showClassroomOperate = -1
          this.selectList(type, name, index)
          this.getClassroomsList(id)
        } else {
          return false
        }
      },

      // 
      selectClassroom (type, id, name, index) {
        if (this.clickList === true) {
          this.classroomId = id// id
          this.selectList(type, name, index)
        } else {
          return false
        }
      },

      // 
      selectList (type, name, index) {
        let campusesList = document.getElementsByClassName('campusesList')
        let buildingsList = document.getElementsByClassName('buildingsList')
        let classroomsList = document.getElementsByClassName('classroomsList')
        if (type === 0) {// 
          // 
          this.changeCSS(campusesList, name)
          // div
          this.showCampusOperate = index
          // this.showOperate = this.showOperate === index ? -1 : index
        } else if (type === 1) {// 
          this.changeCSS(buildingsList, name)
          this.showBuildingOperate = index
        } else if (type === 2) {// 
          this.changeCSS(classroomsList, name)
          this.showClassroomOperate = index
        }
      },

      // 
      changeCSS (list, name) {
        for (let i = 0; i < list.length; i++) {
          //--
          let dataName = list[i].innerText.split(' ')[0]
          // 
          dataName = dataName.replace(/\s/g, '')
          if (name === dataName) {//
            list[i].style.background = '#BBB'
          } else {
            list[i].style.background = '#FFF'
          }
        }
      },

      // 
      addNewDiv (type) {
        if (this.modifyBtn === false) {
          return false
        } else {
          // div
          let div = document.getElementsByClassName('el-card__body')
          let newDiv = document.createElement('div')
          newDiv.setAttribute('class', 'groupList')// class
          newDiv.innerHTML = '<input type="text" style="text-align: center"/>'// input
          if (type === 0) {// 
            this.addCampus(newDiv)
            this.haveCampuses = false
          } else if (type === 1) {// 
            newDiv.innerHTML = '<input type="text" value="( )" style="text-align: center"/>'
            this.addBuilding(newDiv)
          } else if (type === 2) {// 
            this.addClassroom(newDiv)
          }
          div[type].appendChild(newDiv)// div
          newDiv.childNodes[0].focus()
        }
      },

      // 
      addCampus (newDiv) {
        let self = this
        newDiv.childNodes[0].addEventListener('blur', function () {// input
          let newCampus = newDiv.childNodes[0].value// 
          if (newCampus) {// 
            $.ajax({
              type: 'POST',
              url: self.api + 'schoolZones',
              dataType: 'json',
              data: {'name': newCampus},
              success (data) {
                if (data === 1) {// 
                  newDiv.remove()
                  self.getCampusesList()
                  self.$message({
                    message: '',
                    type: 'success'
                  })
                  self.modifyBtn = true
                } else if (data.status === 1) {
                  self.clickList = false// 
                  newDiv.childNodes[0].focus()// input
                  self.$message({
                    message: '',
                    type: 'warning'
                  })
                  self.modifyBtn = false
                } else {
                  self.modifyBtn = true
                  newDiv.remove()
                  self.getCampusesList()
                  self.$message.error('')
                }
              }
            })
          } else {// 
            self.clickList = true
            newDiv.remove()
          }
        })
      },

      // 
      addBuilding (newDiv) {
        if (this.campusId === -1) {// 
          newDiv.childNodes[0].addEventListener('blur', function () {// input
            newDiv.remove()
          })
          alert('')
        } else {// 
          let self = this
          this.haveBuildings = false
          newDiv.childNodes[0].addEventListener('blur', function () {// input
            let buildingData = newDiv.childNodes[0].value// 
            let newBuilding = buildingData.split('(')[0]// 
            let newFloor = parseInt(buildingData.substring(buildingData.indexOf('(') + 1, buildingData.indexOf(')')))// 
            if (newBuilding && newFloor) {// &
              $.ajax({
                type: 'POST',
                url: self.api + 'buildings',
                headers: {
                  'Content-Type': 'application/json',
                },
                dataType: 'json',
                data: JSON.stringify({
                  'zoneId': self.campusId,
                  'name': newBuilding,
                  'floors': newFloor
                }),
                success (data) {
                  if (data === 1) {// 
                    newDiv.remove()
                    self.getBuildingsList(self.campusId)
                    self.$message({
                      message: '',
                      type: 'success'
                    })
                    self.modifyBtn = true
                  } else if (data.status === 1) {
                    self.clickList = false// 
                    newDiv.childNodes[0].focus()// input
                    self.$message({
                      message: '',
                      type: 'warning'
                    })
                    self.modifyBtn = false
                  } else {
                    self.modifyBtn = true
                    newDiv.remove()
                    self.getBuildingsList(self.campusId)
                    self.$message.error('')
                  }
                }
              })
            } else if (!newBuilding && newFloor) {// 
              self.clickList = false
              self.modifyBtn = false
              self.$message.error('')
              newDiv.childNodes[0].focus()
            } else if (newBuilding && !newFloor) {// 
              self.clickList = false
              self.modifyBtn = false
              self.$message.error('')
              newDiv.childNodes[0].focus()
            } else {
              self.clickList = true
              self.modifyBtn = true
              newDiv.remove()// div
            }
          })
        }
      },

      // 
      addClassroom (newDiv) {
        if (this.campusId !== -1 && this.buildingId !== -1) {// 
          let self = this
          this.haveClassrooms = false
          // div
          newDiv.childNodes[0].addEventListener('blur', function () {//  input
            let newClassroom = newDiv.childNodes[0].value// 
            let roomFloor = parseInt(newClassroom.replace(/[^0-9]/ig, '').substr(0, 1))// 
            if (newClassroom && roomFloor) {// &
              $.ajax({
                type: 'POST',
                url: self.api + 'rooms',
                headers: {
                  'Content-Type': 'application/json',
                },
                dataType: 'json',
                data: JSON.stringify({
                  'buildingId': self.buildingId,
                  'name': newClassroom,
                  'floor': roomFloor
                }),
                success (data) {
                  if (data === 1) {// 
                    newDiv.remove()
                    self.getClassroomsList(self.buildingId)
                    self.$message({
                      message: '',
                      type: 'success'
                    })
                    self.modifyBtn = true
                  } else if (data.status === 1) {
                    self.clickList = false// 
                    newDiv.childNodes[0].focus()// input
                    self.$message({
                      message: '',
                      type: 'warning'
                    })
                    self.modifyBtn = false
                  } else {
                    self.modifyBtn = true
                    newDiv.remove()
                    self.getClassroomsList(self.buildingId)
                    self.$message.error('')
                  }
                }
              })
            } else if (newClassroom && !roomFloor) {
              self.clickList = false// 
              self.modifyBtn = false
              newDiv.childNodes[0].focus()// input
              self.$message.error('')
            } else {
              self.clickList = true
              self.modifyBtn = true
              newDiv.remove()// div
            }
          })
        } else if (this.campusId === -1) {
          newDiv.childNodes[0].addEventListener('blur', function () {// input
            newDiv.remove()
          })
          alert('')
        } else if (this.buildingId === -1) {
          newDiv.childNodes[0].addEventListener('blur', function () {// input
            newDiv.remove()
          })
          alert('')
        }
      },

      // 
      modifyNewDiv (type, index, name, floor) {
        let div = document.getElementsByClassName('el-card__body')
        let newDiv = document.createElement('div')
        newDiv.setAttribute('class', 'groupList')// class
        newDiv.innerHTML = '<input type="text" style="text-align: center"/>'// input
        div[type].insertBefore(newDiv, div[type].childNodes[index + 1])// inputdiv
        div[type].childNodes[index + 2].style.display = 'none'// div
        newDiv.childNodes[0].focus()// input
        newDiv.childNodes[0].value = name// 
        if (type === 0) {// 
          this.modifyCampus(div[type], newDiv, index)
        } else if (type === 1) {// 
          newDiv.childNodes[0].value = name + '(' + floor + ')'
          this.modifyBuilding(div[type], newDiv, index)
        } else if (type === 2) {// 
          this.modifyClassroom(div[type], newDiv, index)
        }
      },

      // 
      modifyCampus (div, newDiv, index) {
        let self = this
        newDiv.childNodes[0].addEventListener('blur', function () {// input
          let newCampus = newDiv.childNodes[0].value// 
          if (newCampus) {// 
            $.ajax({
              type: 'PUT',
              url: self.api + 'schoolZones',
              headers: {'Content-Type': 'application/json',},
              dataType: 'json',
              data: JSON.stringify({
                'id': self.campusId,
                'name': newCampus
              }),
              success (data) {
                if (data === 1) {
                  self.getCampusesList()
                  newDiv.remove()
                  // div
                  div.childNodes[index + 1].style.display = 'block'
                  div.childNodes[index + 1].style.background = '#BBB'
                  self.$message({
                    message: '',
                    type: 'success'
                  })
                  self.modifyBtn = true
                } else if (data.status === 1) {
                  self.clickList = false// 
                  newDiv.childNodes[0].focus()// input
                  self.$message({
                    message: '',
                    type: 'warning'
                  })
                  self.modifyBtn = false
                } else {
                  self.getCampusesList()
                  self.modifyBtn = true
                  newDiv.remove()
                  // div
                  div.childNodes[index + 1].style.display = 'block'
                  div.childNodes[index + 1].style.background = '#BBB'
                  self.$message.error('')
                }
              }
            })
          } else {// 
            self.$message.error('')
            self.clickList = false
            self.modifyBtn = false
            newDiv.childNodes[0].focus()// input
          }
        })
      },

      // 
      modifyBuilding (div, newDiv, index) {
        let self = this
        newDiv.childNodes[0].addEventListener('blur', function () {// input
          let buildingData = newDiv.childNodes[0].value// 
          let newBuilding = buildingData.split('(')[0]// 
          let newFloor = parseInt(buildingData.substring(buildingData.indexOf('(') + 1, buildingData.indexOf(')')))// 
          if (newBuilding && newFloor) {// 
            $.ajax({
              type: 'PUT',
              url: self.api + 'buildings',
              headers: {
                'Content-Type': 'application/json',
              },
              dataType: 'json',
              data: JSON.stringify({
                'id': self.buildingId,
                'name': newBuilding,
                'floors': newFloor,
                'zoneId': self.campusId
              }),
              success (data) {// 
                if (data === 1) {
                  self.getBuildingsList(self.campusId)
                  newDiv.remove()// div
                  div.childNodes[index + 1].style.display = 'block'// div
                  div.childNodes[index + 1].style.background = '#BBB'// div
                  self.$message({
                    message: '',
                    type: 'success'
                  })
                  self.modifyBtn = true
                } else if (data.status === 1) {
                  self.clickList = false// 
                  newDiv.childNodes[0].focus()// input
                  self.$message({
                    message: '',
                    type: 'warning'
                  })
                  self.modifyBtn = false
                } else {
                  self.getBuildingsList(self.campusId)
                  newDiv.remove()// div
                  div.childNodes[index + 1].style.display = 'block'// div
                  div.childNodes[index + 1].style.background = '#BBB'// div
                  self.$message.error('')
                  self.modifyBtn = true
                }
              },
              error () {
                self.getBuildingsList(self.campusId)
                self.modifyBtn = true
                newDiv.remove()// div
                div.childNodes[index + 1].style.display = 'block'// div
                div.childNodes[index + 1].style.background = '#BBB'// div
                console.log('error')
              }
            })
          } else {
            self.$message.error('"()"')
            self.clickList = false
            self.modifyBtn = false
            newDiv.childNodes[0].focus()// input
          }
        })
      },

      // 
      modifyClassroom (div, newDiv, index) {
        let self = this
        newDiv.childNodes[0].addEventListener('blur', function () {// input
          let newClassroom = newDiv.childNodes[0].value// 
          let roomFloor = parseInt(newClassroom.replace(/[^0-9]/ig, '').substr(0, 1))// 
          if (newClassroom) {// 
            $.ajax({
              type: 'PUT',
              url: self.api + 'rooms',
              headers: {
                'Content-Type': 'application/json',
              },
              dataType: 'json',
              data: JSON.stringify({
                'id': self.classroomId,
                'name': newClassroom,
                'floors': roomFloor,
                'buildingId': self.buildingId
              }),
              success (data) {
                if (data === 1) {
                  self.getClassroomsList(self.buildingId)
                  newDiv.remove()// div
                  div.childNodes[index + 1].style.display = 'block'
                  div.childNodes[index + 1].style.background = '#BBB'
                  self.$message({
                    message: '',
                    type: 'success'
                  })
                  self.modifyBtn = true
                } else if (data.status === 1) {
                  self.clickList = false// 
                  newDiv.childNodes[0].focus()// input
                  self.$message({
                    message: '',
                    type: 'warning'
                  })
                  self.modifyBtn = false
                } else {
                  self.getClassroomsList(self.buildingId)
                  self.modifyBtn = true
                  newDiv.remove()// div
                  div.childNodes[index + 1].style.display = 'block'
                  div.childNodes[index + 1].style.background = '#BBB'
                  self.$message.error('')
                }
              }
            })
          } else {// 
            self.$message.error('')
            self.clickList = false
            self.modifyBtn = false
            newDiv.childNodes[0].focus()
          }
        })
      },

      // 
      deleteGroup (type, id) {
        if (type === 0) {
          this.delGroupType = ''
        } else if (type === 1) {
          this.delGroupType = ''
        } else if (type === 2) {
          this.delGroupType = ''
        }
        this.delId = id
        this.delGroup = true
      },

      // 
      clearCSS (type) {
        console.log(type)
        let campusesList = document.getElementsByClassName('campusesList')
        let buildingsList = document.getElementsByClassName('buildingsList')
        let classroomsList = document.getElementsByClassName('classroomsList')
        if (type === 0) {// 
          this.showCampusOperate = -1
          for (let i = 0; i < campusesList.length; i++) {
            campusesList[i].style.background = '#FFF'
          }
        } else if (type === 1) {// 
          this.showBuildingOperate = -1
          for (let i = 0; i < buildingsList.length; i++) {
            buildingsList[i].style.background = '#FFF'
          }
        } else if (type === 2) {// 
          this.showClassroomOperate = -1
          for (let i = 0; i < classroomsList.length; i++) {
            classroomsList[i].style.background = '#FFF'
          }
        }
      },

      // 
      getCampusesList () {
        let self = this
        this.clickList = true
        $.ajax({
          type: 'GET',
          async: false,
          url: this.api + 'schoolZones',
          success (data) {
            if (data.length === 0) {
              self.haveCampuses = true
            } else {
              self.haveCampuses = false
            }
            self.allCampuses = data
          },
          error () {
            console.log('')
          }
        })
      },

      // 
      getBuildingsList (id) {
        let self = this
        this.clickList = true
        $.ajax({
            type: 'GET',
            url: this.api + 'buildings',
            data: {'zoneId': id},
            success (data) {
              // self.$emit('updateNav')
              if (data.length === 0) {
                self.haveBuildings = true
              } else {
                self.haveBuildings = false
              }
              self.buildingsData = data
            },
            error () {
              console.log('')
            }
          }
        )
      },

      // 
      getClassroomsList (id) {
        let self = this
        this.clickList = true

        // id
        $.ajax({
          type: 'GET',
          url: this.api + 'rooms/' + id,
          success (data) {
            if (data.length === 0) {
              self.haveClassrooms = true
            } else {
              self.haveClassrooms = false
            }
            self.classroomsData = data
          }
        })
      },

      // 
      showBatchAddition () {
        this.batchAddition = true
      },

      // 
      closeModel () {
        this.delGroup = false
        this.batchAddition = false
      },

      // 
      closeAllModel () {
        this.manageGroups = false
        this.$emit('closeModel')
      }
    },
    mounted () {
      this.getCampusesList()
    }
  }
</script>

<style scoped>
  @import '../../assets/public/model.css';
  @import '../../assets/manageGroups.css';
</style>

<style>
  .groupList {
    padding: 2px 5px 5px 5px;
    border-bottom: 1px solid #BBBBBB;
  }

  input {
    width: 50%;
    padding: 0 0 3px 3px;
    border-top: 0;
    border-left: 0;
    border-right: 0;
    border-bottom: 1px solid #BBB;
    outline: none;
  }
</style>



