<template>
  <div>
      <el-table
    :data="tableData"
    style="width: 100%">
    <el-table-column
      label=""
      align = "center"
      >
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.name }}</span>
      </template>
    </el-table-column>
    <el-table-column
      label=""
      align = "center"
      >
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.olderTime }}</span>
      </template>
    </el-table-column>
     <el-table-column
      label=""
      align = "center"
      >
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.operatTime }}</span>
      </template>
    </el-table-column>
        <el-table-column
      label=""
      align = "center"
      >
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.operatPerson }}</span>
      </template>
    </el-table-column>
     </el-table-column>
        <el-table-column
      label=""
      align = "center"
      >
      <template slot-scope="scope">
        <span style="margin-left: 10px">{{ scope.row.status }}</span>
      </template>
    </el-table-column>
    <el-table-column label="" align = "center">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)"></el-button>
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>


  <el-dialog
  title=""
  :visible.sync="editModel"
  width="30%"
  :before-close="handleClose">
  <div style="text-align:center">
       <el-date-picker
                v-model="startTime"
                type="datetime"
                format="yyyy-MM-dd HH:mm"
                placeholder=""
                size="mini"
                :picker-options="pickerOptions"
                @change="initOptions2"
              >
              </el-date-picker>
  </div>
  <span slot="footer" class="dialog-footer">
    <el-button @click="editModel = false"> </el-button>
    <el-button type="primary" @click="editModel = false"> </el-button>
  </span>
</el-dialog>
  </div>
</template>

<script>
export default {
data() {
      return {
          editModel:false,//
          startTime:"",
        tableData: [{
          name: '209',
          olderTime: '2019-12-26 13:00',//
          operatTime: '2019-12-26 12:00',//
          operatPerson:"",//
          status:""//
        }, ]
      }
    },
    methods: {
      handleEdit(index, row) {
        console.log(index, row);
        this.editModel = true
      },
    
      handleDelete(index, row) {
        this.$confirm(`${row.name} ?`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
      }
    }
}
</script>

<style>

</style>


