<template>
  <div>
    <div class="tableHead">
      <div class="tableHead-left">
        <i class="el-icon-refresh"></i>
        <el-button
          type="text"
          style="margin-left:20px"
          @click="centerDialogVisible = true"
          ></el-button
        >
        <el-button type="text" style="margin-left:20px" @click="batchOperat"
          ></el-button
        >
        <el-button type="text" style="margin-left:20px" @click="batchExport"
          ></el-button
        >
      </div>
      <div class="tableHead-right">
        <el-button
          size="mini"
          style="height: 30px;margin:auto 50px "
          @click="searchClick"
          ></el-button
        >
        <template>
          <el-select
            v-model="selectfloorSearch"
            placeholder=""
            size="mini"
            style="width:120px;"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </template>
      </div>
    </div>

    <template>
      <el-table :data="tableData" style="width: 100%">
        <el-table-column
          type="expand"
          label=""
          width="78px"
          align="center"
          style="padding:0;height: 50px;"
        >
          <template slot-scope="props" style="padding:0">
            <el-form
              label-position="left"
              inline
              class="demo-table-expand"
              style="padding:0;height: 40px;"
            >
              <el-form-item label="IP:">
                <span>{{ props.row.ip }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.location }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label=":">
                <span>{{ props.row.name }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column label="" prop="type" align="center">
        </el-table-column>
        <el-table-column label="" prop="brandge" align="center">
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope"> {{ scope.row.ter }} </template>
        </el-table-column>
        <el-table-column label="" prop="status" align="center">
        </el-table-column>
        <el-table-column label="" prop="nums" align="center">
        </el-table-column>
        <el-table-column label="" prop="control" width="200" align="center">
          <template slot-scope="props">
            <el-select
              v-model="controlItem[props.$index]['control' + props.$index]"
              placeholder=""
              size="small"
              :disabled="controllIsDone"
              style="width:150px;"
              @change="
                controllSelectClick(
                  props.row.id,
                  controlItem[props.$index]['control' + props.$index]
                )
              "
            >
              <!--  -->
              <el-option
                v-for="item in props.row.control"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="" prop="selfControl" align="center">
          <template slot-scope="props">
            <el-switch
              v-if="props.row.selfControl !== 'null'"
              v-model="props.row.selfControl"
              active-color="#13ce66"
              inactive-color="#ff3342"
              @change="switchChange($event, props.row.id)"
            >
            </el-switch>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="selfControl"
          align="center"
          width="200"
        >
          <template slot-scope="props">
            <template v-if="props.row.jdControl !== 'null'">
              <el-popover
                v-if="props.row.selfControl"
                placement="top-start"
                title=""
                width="100"
                trigger="hover"
                content=""
              >
                <el-switch
                  slot="reference"
                  disabled="!props.row.selfControl"
                  v-model="props.row.jdControl"
                  active-color="#13ce66"
                  inactive-color="#ff3342"
                  @change="
                    switchChange2($event, props.row.id, props.row.selfControl)
                  "
                >
                </el-switch>
              </el-popover>
              <el-switch
                v-if="!props.row.selfControl"
                slot="reference"
                v-model="props.row.jdControl"
                active-color="#13ce66"
                inactive-color="#ff3342"
                @change="
                  switchChange2($event, props.row.id, props.row.selfControl)
                "
              >
              </el-switch>
            </template>
            <span v-else></span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          prop="manage"
          align="center"
          width="200px"
        >
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="
                handleEdit(
                  scope.row.id,
                  scope.row.ip,
                  scope.row.location,
                  scope.row.name,
                  scope.row.numCode
                )
              "
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.row.name, scope.row.id)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <div style="text-align:center;margin:20px 0 0 0 ">
        <!-- <el-switch v-model="pages"> </el-switch> -->
        <el-pagination
          :hide-on-single-page="pages"
          :total="total"
          :page-size="10"
          layout="prev, pager, next"
          @current-change="changePage"
          @prev-click="prevClick"
          @next-click="nextClick"
        >
        </el-pagination>
      </div>
    </template>
    <!--  -->

    <el-dialog
      title=""
      :visible.sync="centerDialogVisible"
      width="650px"
      style="border-bottom: 1px #eee solid;"
    >
      <ul class="dialogContent">
        <li class="dialogContentItem">
          <span class="dItemText">:</span>
          <template>
            <el-select
              v-model="addSchoolAera"
              placeholder=""
              size="small"
              :loading="addSchoolAeraOptions.length == 0"
              @focus="selectSchool"
            >
              <el-option
                v-for="item in addSchoolAeraOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText">:</span>
          <template>
            <el-select
              v-model="addBuilding"
              :disabled="addSchoolAera == ''"
              placeholder=""
              size="small"
              :loading="addBuildingOptions.length == 0"
              @focus="selectbuilding"
            >
              <el-option
                v-for="item in addBuildingOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText">:</span>
          <template>
            <el-select
              v-model="addFloor"
              :disabled="addBuilding == ''"
              placeholder=""
              size="small"
              :loading="addFloorOptions.length == 0"
              @focus="selectFloor"
            >
              <el-option
                v-for="item in addFloorOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText">:</span>
          <template>
            <el-select
              v-model="addRoom"
              :disabled="addFloor == ''"
              placeholder=""
              size="small"
              @focus="selectRoom"
            >
              <!-- :loading="addRoomOptions.length == 0" -->
              <el-option
                v-for="item in addRoomOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText">:</span>
          <template>
            <el-select
              v-model="addBrand"
              @focus="selectBrand"
              placeholder=""
              size="small"
            >
              <el-option
                v-for="item in addBrandOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </template>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText">:</span>
          <template>
            <el-select
              v-model="addType"
              :disabled="addBrand == ''"
              placeholder=""
              size="small"
              @focus="selectType"
            >
              <el-option
                v-for="item in addTypeOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                append-to-body
              >
              </el-option>
            </el-select>
          </template>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText numStyle">:</span>
          <el-input
            v-model="addId"
            placeholder=""
            size="small"
            style="width:215px"
          ></el-input>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText ipStyle">IP:</span>
          <el-input
            v-model="addIp"
            placeholder="ip:xxx.xx.xx.xx"
            size="small"
            style="width:215px"
          ></el-input>
        </li>
        <li class="dialogContentItem">
          <span class="dItemText ipStyle">:</span>
          <el-input
            v-model="addPort"
            placeholder=":8081"
            size="small"
            style="width:215px"
          ></el-input>
        </li>
        <li class="dialogContentItem ">
          <span class="dItemText nameStyle">:</span>
          <el-input
            v-model="addName"
            placeholder=":209-1"
            size="small"
            style="width:215px"
          ></el-input>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer" center>
        <el-button @click="centerDialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click.prevent="addDevice"
          :disabled="
            !(addRoom && addType && addId && addName && addPort && addIp)
          "
          > </el-button
        >
      </span>
    </el-dialog>
    <editDevice
      :showDialog="showDialog"
      :editid="editid"
      :editname="editname"
      :editip="editip"
      :editport="editport"
      :editnum="numCode"
      @editClose="editClose"
    ></editDevice
    ><!--  -->
    <batchDialog
      :showBatch="showBatch"
      @batchOperatClose="batchOperatClose"
    ></batchDialog
    ><!--  -->
    <batchExport
      :showUpLoad="showUpLoad"
      @exportClose="exportClose"
    ></batchExport
    ><!--  -->
  </div>
</template>

<script>
import editDevice from "./editDevice";
import batchDialog from "./batchOperat";
import batchExport from "./upLoad";
import axios from "axios";
import qs from "qs";

export default {
  data() {
    return {
      /* */
      timerId: "", //id
      controllIsDone: false, //
      pages: true,
      pagesNum: 1,
      total: 0,
      schoolLoaded: true, //
      test: "",
      isSearch: false,
      selectfloorSearch: "", //
      addName: "209", //
      addIp: "172.16.22.209", //ip
      addId: "01", //
      addType: "", //
      addBrand: "", //
      addRoom: "", //
      addFloor: "", //
      addBuilding: "", //
      addSchoolAera: "", //
      addPort: "8001", //

      addNameOptions: [], //
      addIpOptions: [], //ip
      addIdOptions: [], //
      addTypeOptions: [], //
      addBrandOptions: [], //
      addRoomOptions: [], //
      addFloorOptions: [], //
      addBuildingOptions: [], //
      addSchoolAeraOptions: [], //

      /*  */
      showDialog: false,
      type: "",
      room: "",
      floor: "",
      build: "",
      school: "",
      brand: "",
      id: "",
      ip: "",
      name: "",
      editid: "",
      editname: "",
      editip: "",
      editport: "",
      numCode: "",

      /*  */
      showUpLoad: false,

      /*  */
      showBatch: false, //
      centerDialogVisible: false, //
      selectBuild: "", //
      searchInput: "", //
      code: this.$route.query.code, //
      buildId: this.$route.query.build
        ? this.$route.query.build
        : localStorage.getItem("initBuildId"), //id
      floorNum: this.$route.query.fNum
        ? this.$route.query.fNum
        : localStorage.getItem("initFloorNum"), //
      schoolId: this.$route.query.sch
        ? this.$route.query.sch
        : localStorage.getItem("initschoolId"), //ID
      controlItem: [], //
      selfBtnValue: "",
      options: [
        //
      ],
      control: [],
      controlItemLength: "", //
      tableData: [
        //
        // {
        //   type: "1P",
        //   brandge: "",
        //   ter: "26",
        //   status: "",
        //   nums: "200",
        //   control: [
        //     {
        //       value: "",
        //       label: ""
        //     },
        //     {
        //       value: "",
        //       label: ""
        //     },
        //     {
        //       value: "20",
        //       label: "20"
        //     },
        //     {
        //       value: "18",
        //       label: "18"
        //     },
        //     {
        //       value: "18",
        //       label: "18"
        //     }
        //   ],
        //   selfControl: false,
        //   jdControl: true,
        //   location: "209",
        //   name: "",
        //   id: "1123",
        //   ip: "172.16.22.18"
        // },
      ]
    };
  },
  components: {
    editDevice,
    batchDialog,
    batchExport
  },
  methods: {
    controllSelectClick(id, event) {
      console.log(id, event);
      this.controllIsDone = true;

      axios
        .get(this.api + "devices/perform", {
          params: { ids: id, itemName: event }
        })
        .then(() => {
          this.controllIsDone = false;
        })
        .catch(() => {
          console.log("");
        });
      setTimeout(() => {
        this.controllIsDone = false;
      }, 500);
    },
    inintList(List, data) {
      data.forEach(item => {
        List.push({
          type: item.model_name,
          brandge: item.brand_name,
          ter: item.log ? item.log.contextT + "" : "",
          status: item.log
            ? (item.log.relayStatus ? "" : "") +
              (item.log.onOffStatus ? "" : "")
            : "",
          nums: item.log ? item.log.electricConsume + "" : "",
          selfControl: item.log
            ? item.log.autoStatus == 0
              ? false
              : true
            : "null",
          control: this.control,
          jdControl: item.log
            ? item.log.relayStatus == 0
              ? false
              : true
            : "null",
          location: item.port,
          name: item.name,
          id: item.id,
          ip: item.ip_address,
          numCode: item.address_code
        });
      });
    },
    changePage($event) {
      //
      console.log($event);
      this.pagesNum = $event;

      if (this.isSearch) {
        this.getPageAfterList();
      } else {
        this.getListData();
      }
    },
    prevClick($event) {
      //
      console.log($event);
    },
    nextClick($event) {
      //
      console.log($event);
    },
    addDevice() {
      //
      let params = {
        name: this.addName,
        ipAddress: this.addIp,
        port: this.addPort,
        addressCode: this.addId,
        roomId: this.addRoom,
        modelId: this.addBuilding
      };
      $.ajax({
        type: "POST",
        url: this.api + "devices",
        data: JSON.stringify(params),
        contentType: "application/json",
        success: data => {
          // 
          console.log(data);
          this.messageSuccess();
          this.centerDialogVisible = false;
        },
        error: () => {
          this.messageErr();
        }
      });
    },
    selectSchool(e) {
      //
      this.addSchoolAeraOptions = [];
      this.addBuildingOptions = [];
      this.addFloorOptions = [];
      this.addFloor = "";
      this.addBuilding = "";
      this.addRoomOptions = [];
      this.addRoom = "";
      this.constructData("schoolZones", this.addSchoolAeraOptions);
    },
    selectbuilding() {
      //
      this.addFloorOptions = [];
      (this.addFloor = ""), //
        (this.addBuildingOptions = []);
      this.addRoomOptions = [];
      this.addRoom = "";
      this.constructData("buildings", this.addBuildingOptions, {
        zoneId: this.addSchoolAera
      });
    },
    selectFloor(params) {
      //
      this.addFloorOptions = [];
      this.addRoomOptions = [];
      this.addRoom = "";
      this.pubilcFnAxios(`buildings/${this.addBuilding}/floors`, {})
        .then(data => {
          console.log(data);
          for (let i = 1; i < data + 1; i++) {
            this.addFloorOptions.push({ label: i + "F", value: i });
          }
        })
        .catch(() => {
          console.log("");
        });
    },
    selectRoom() {
      //
      this.addRoomOptions = [];
      this.constructData(
        `rooms/${this.addBuilding}/${this.addFloor}`,
        this.addRoomOptions,
        {}
      );
    },
    selectBrand() {
      //
      this.addBrandOptions = [];
      this.addTypeOptions = [];
      this.addType = "";
      this.pubilcFnAxios("models/brands", {})
        .then(data => {
          for (let i = 0; i < data.length; i++) {
            this.addBrandOptions.push({ label: data[i], value: data[i] });
          }
        })
        .catch(() => {
          console.log("");
        });
    },
    selectType() {
      //
      this.addTypeOptions = [];
      this.constructData(`/models/${this.addBrand}`, this.addTypeOptions, {});
    },
    constructData(urlString, obj, params) {
      //
      this.pubilcFnAxios(urlString, params)
        .then(data => {
          console.log(data);
          data.forEach(item => {
            obj.push({
              value: item.id,
              label: item.name || item.modelName
            });
          });
          this.schoolLoaded = false;
        })
        .catch(() => {
          console.log("");
        });
    },

    pubilcFnAxios(urlString, params, method) {
      //axios
      return new Promise((resolve, reject) => {
        axios
          .get(this.api + urlString, { params: params })
          .then(res => {
            resolve(res.data);
          })
          .catch(err => {
            reject("get");
          });
      });
    },

    buildControlModel() {
      //
      let arr = [];
      let i = 0;
      for (i; i < this.controlItemLength; i++) {
        arr.push({ ["control" + i]: "" });
      }
      console.log(arr);
      return arr;
    },
    switchChange(el, id) {
      //
      //
      console.log(el, id);
      if (el) {
        this.pubilcFnAxios("devices/auto/on", { ids: [id].toString() })
          .then(data => {
            console.log("");
          })
          .catch(() => {
            console.log("");
          });
      } else {
        this.pubilcFnAxios("devices/auto/off", { ids: [id].toString() })
          .then(data => {
            console.log("");
          })
          .catch(() => {
            console.log("");
          });
      }
    },
    switchChange2(el, id, selfStatus) {//
      // this.tableData[id].selfControl = !this.tableData[id].selfControl;
      console.log(id);
      if (el) {
        this.pubilcFnAxios("devices/relay/on", { ids: [id].toString() })
          .then(data => {
            console.log("");
          })
          .catch(() => {
            console.log("");
          });
      } else {
        this.pubilcFnAxios("devices/relay/off", { ids: [id].toString() })
          .then(data => {
            console.log("");
          })
          .catch(() => {
            console.log("");
          });
      }
    },
    searchClick() {
      //
      this.pubilcFnAxios(
        `devices/${this.schoolId || localStorage.getItem("initschoolId")}/${this
          .buildId || localStorage.getItem("initBuildId")}/${
          this.selectfloorSearch
        }`
      )
        .then(data => {
          this.tableData = [];
          this.isSearch = true;
          this.total = data.total;
          this.controlItemLength = data.list.length; //
          this.controlItem = this.buildControlModel();
          this.inintList(this.tableData, data.list);
        })
        .catch(() => {
          console.log("");
        });
    },
    editClose() {
      //
      console.log("");
      this.showDialog = false;
    },
    batchOperatClose() {
      //
      console.log("");
      this.showBatch = false;
    },
    handleEdit(id, ip, port, name, code) {
      //

      this.editid = id.toString();
      this.editname = name;
      this.editip = ip;
      this.editport = port.toString();
      this.numCode = code.toString();
      this.showDialog = true;
      console.log(id, ip, port, name, code);
    },
    open(name, id) {
      //
      this.$confirm(`${name}, ?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          axios
            .delete(this.api + `/devices/${id}`)
            .then(res => {
              console.log(res.data);
              this.$message({
                type: "success",
                message: "!"
              });
              if (this.isSearch) {
                this.getPageAfterList();
              } else {
                this.getListData();
              }
            })
            .catch(() => {
              this.$message({
                type: "error",
                message: "!"
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    messageSuccess() {
      this.$message({
        showClose: true,
        message: "",
        type: "success"
      });
    },
    messageErr() {
      this.$message({
        showClose: true,
        message: "",
        type: "error"
      });
    },
    handleDelete(name, id) {
      //
      this.open(name, id);
    },
    batchOperat() {
      //
      this.showBatch = true;
    },
    batchExport() {
      //
      this.showUpLoad = true;
    },
    exportClose() {
      //
      this.showUpLoad = false;
    },
    openMessage() {
      this.$notify({
        title: "",
        message: "",
        type: "warning",
        duration: 0,
        onClick: this.checkedInfo
      });
    },
    checkedInfo() {
      this.$router.push("/admin/waringInfo");
      console.log("");
    },
    constructSearchInput() {
      //
      for (let i = 0; i < this.floorNum; i++) {
        this.options.push({ value: `${i + 1}`, label: `${i + 1}F` });
      }
    },
    getListData() {
      let params = {
        pageNum: this.pagesNum,
        pageSize: 10
      };
      this.pubilcFnAxios(
        `devices/${this.schoolId || localStorage.getItem("initschoolId")}/${this
          .buildId || localStorage.getItem("initBuildId")}`,
        params
      )
        .then(data => {
          this.tableData = [];
          this.total = data.total;
          this.controlItemLength = data.list.length; //
          this.controlItem = this.buildControlModel();
          this.inintList(this.tableData, data.list);
        })
        .catch(() => {
          console.log("");
        });
    },
    getPageAfterList() {
      let params = {
        pageNum: this.pagesNum,
        pageSize: 10
      };
      this.pubilcFnAxios(
        `devices/${this.schoolId || localStorage.getItem("initschoolId")}/${this
          .buildId || localStorage.getItem("initBuildId")}/${
          this.selectfloorSearch
        }`,
        params
      )
        .then(data => {
          this.tableData = [];
          this.isSearch = true;
          this.total = data.total;
          this.controlItemLength = data.list.length; //
          this.controlItem = this.buildControlModel();
          this.inintList(this.tableData, data.list);
        })
        .catch(() => {
          console.log("");
        });
    },
    refresh() {
      this.timerId = setInterval(() => {
        console.log("" + new Date());
        if (this.isSearch) {
          this.getPageAfterList();
        } else {
          this.getListData();
        }
      }, 20000);
    },
    clear() {
      clearInterval(this.timerId); //
      this.timerId = null; //null
    }
  },
  watch: {
    $route() {
      //
      // this.code = this.$route.query.b;
      this.tableData = [];
      this.buildId = this.$route.query.build;
      this.floorNum = this.$route.query.fNum;
      this.schoolId = this.$route.query.sch;
      localStorage.setItem("initschoolId", this.$route.query.sch);
      localStorage.setItem("initBuildId", this.$route.query.build);
      localStorage.setItem("initFloorNum", this.$route.query.fNum);
      this.options = [];
      this.constructSearchInput();
      this.pubilcFnAxios("items")
        .then(data => {
          this.control = [];
          for (let i = 0; i < data.length; i++) {
            this.control.push({ label: data[i], value: data[i] });
          }
          this.getListData();
        })
        .catch(() => {
          console.log("");
        });
    }
  },
  mounted() {
    //
    // this.openMessage();
    this.constructSearchInput();
    console.log(this.$route.query.code);
    this.pubilcFnAxios("items")
      .then(data => {
        this.control = [];
        for (let i = 0; i < data.length; i++) {
          this.control.push({ label: data[i], value: data[i] });
        }
        this.getListData();
      })
      .catch(() => {
        console.log("");
      });
    // this.refresh()
  },
  destroyed() {
    console.log("");
    this.clear();
  }
};
</script>

<style scoped>
.tableHead {
  height: 50px;
  border-bottom: 1px solid #000;
  line-height: 50px;
  /* padding: 0 0 0 5px; */
}
.tableHead .tableHead-left {
  display: inline-block;
  width: 49%;
}

.el-icon-refresh {
  float: left;
  margin-left: 20px;
  font-size: 34px;
  font-weight: 900;
  color: #5daf34;
  padding-top: 7px;
}

.tableHead-left {
  float: left;
}

.tableHead .tableHead-right {
  display: inline-flex;
  flex-direction: row-reverse;
  width: 50%;
}
.tableHead .tableHead-right .el-input-sellf {
  position: relative;
  font-size: 14px;
  display: inline-block;
  /* width: 100%; */
}
.tableHead .tableHead-right .tableSearch {
  border-radius: 5px;
  height: 40px;
  width: 50px;
}

.dialogContent {
  /* background: red; */
  height: 100%;
  width: 100%;
  text-align: center;
}
.dialogContent .dialogContentItem {
  height: 30px;
  margin: 0 0 20px 0;
}
.dialogContent .dialogContentItem .dItemText {
  margin-right: 15px;
}
.numStyle {
  margin: 0 0 20px -27px;
}
.ipStyle {
  margin: 0 0 20px -15px;
}
.nameStyle {
  margin: 0 0 20px -29px;
}
</style>



